.details {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 50px 0;
  box-shadow: 0 0 5px #ccc;
}
.details img {
  max-width: 500px;
  min-width: 290px;
  height: 300px;
  display: block;
  object-fit: cover;
  margin: 25px;
}
.details .box {
  max-width: 500px;
  min-width: 290px;
  margin: 25px;
}
.box .row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}

.box .row h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
}

.box .row span {
  color: crimson;
}
.box p {
  line-height: 1.5;
  margin: 15px 0;
}
.cart {
  background: #333;
  color: white;
  text-decoration: none;
  padding: 20px 20px;
  display: inline-block;
  border-radius: 5px;
}
